<script setup>
import {
  ref,
  watch,
  onMounted,
  onUnmounted,
  computed,
  nextTick,
  watchEffect,
} from 'vue';
import { useRouter } from 'vue-router';

// Store
import { useAppStore } from '@/store/app/app.js';
const appStore = useAppStore();

// Functions
import { lmnFetch } from '@/assets/js/functions/lmnFetch.js';
import { notify } from '@/assets/js/functions/notify.js';
import { inSearch } from '@/assets/js/functions/inSearch.js';
import { useColorScheme } from '@/assets/js/functions/useColorScheme.js';
import { useSearchHistory } from '@/composables/useSearchHistory.js';
import { applyFilters } from '@/assets/js/functions/HooksSystem.js';

// Comps
import AppIcon from '@/components/utility/icons/index.vue';
import AppButton from '@/components/utility/app-button/index.vue';
import Modal from '@/components/utility/modal/index.vue';
import AppInput from '@/components/utility/text-input/index.vue';

// Refs
const { userPreference, setColorScheme, prefersDark, globalOverride } =
  useColorScheme();
const {
  searchHistory,
  recentlyAccessed,
  frequentlyUsed,
  addToHistory,
  clearHistory,
  trackAccess,
  getFilteredRecentlyAccessed,
  getFilteredFrequentlyUsed,
} = useSearchHistory();
const router = useRouter();
const showModal = ref(false);
const searchQuery = ref('');
const searchmodal = ref(null);
const searchResults = ref({});
const isLoading = ref(false);
const searchInput = ref('');
const activeResultIndex = ref(-1);
const showHistoryDropdown = ref(false);
const hasSeenOnboarding = ref(false);
const showOnboarding = ref(false);

/**
 * Creates a new post
 */
const newPost = async () => {
  window.location.href = `${appStore.state.adminUrl}post-new.php`;
};

/**
 * Creates a new page
 */
const newPage = async () => {
  window.location.href = `${appStore.state.adminUrl}post-new.php?post_type=page`;
};

/**
 * Get current page identifier for magic dark mode
 * @returns {string} Page identifier (URL path or page parameter)
 */
const getCurrentPageIdentifier = () => {
  const url = new URL(window.location.href);
  const urlParams = new URLSearchParams(url.search);

  // If there's a 'page' parameter, use that (for plugin settings pages)
  if (urlParams.has('page')) {
    return urlParams.get('page');
  }

  // Otherwise use the pathname
  const pathname = url.pathname;
  const adminPath =
    appStore.state.adminUrl?.replace(window.location.origin, '') ||
    '/wp-admin/';

  // Extract the page filename from the path
  if (pathname.includes(adminPath)) {
    const relativePath = pathname.replace(adminPath, '');
    return relativePath || 'index.php';
  }

  // Fallback to full pathname
  return pathname;
};

/**
 * Check if current page is in the magic dark mode pages array
 * @returns {boolean} True if page should have magic dark mode
 */
const isCurrentPageInMagicDarkModeList = () => {
  const magicDarkModePages =
    appStore.state.uixpress_settings?.magic_dark_mode_pages || [];
  const currentPage = getCurrentPageIdentifier();
  return magicDarkModePages.includes(currentPage);
};

/**
 * Add current page to magic dark mode pages list
 */
const enableMagicDarkModeForCurrentPage = async () => {
  const currentPage = getCurrentPageIdentifier();
  const magicDarkModePages =
    appStore.state.uixpress_settings?.magic_dark_mode_pages || [];

  // Don't add if already in list
  if (magicDarkModePages.includes(currentPage)) {
    return;
  }

  const updatedPages = [...magicDarkModePages, currentPage];

  try {
    const response = await lmnFetch({
      endpoint: 'wp/v2/settings',
      type: 'POST',
      data: {
        uixpress_settings: {
          ...appStore.state.uixpress_settings,
          magic_dark_mode_pages: updatedPages,
        },
      },
    });

    if (response && response.data) {
      appStore.updateState(
        'uixpress_settings',
        response.data.uixpress_settings
      );
      notify({
        message: __('Magic dark mode enabled for this page', 'uixpress'),
        type: 'success',
      });

      // Dispatch settings update event
      document.dispatchEvent(
        new CustomEvent('uipx-settings-update', {
          detail: { magic_dark_mode_pages: updatedPages },
        })
      );
    }
  } catch (error) {
    notify({
      message: __('Failed to enable magic dark mode', 'uixpress'),
      type: 'error',
    });
    console.error('Error enabling magic dark mode:', error);
  }
};

/**
 * Remove current page from magic dark mode pages list
 */
const disableMagicDarkModeForCurrentPage = async () => {
  const currentPage = getCurrentPageIdentifier();
  const magicDarkModePages =
    appStore.state.uixpress_settings?.magic_dark_mode_pages || [];

  // Don't remove if not in list
  if (!magicDarkModePages.includes(currentPage)) {
    return;
  }

  const updatedPages = magicDarkModePages.filter(
    (page) => page !== currentPage
  );

  try {
    const response = await lmnFetch({
      endpoint: 'wp/v2/settings',
      type: 'POST',
      data: {
        uixpress_settings: {
          ...appStore.state.uixpress_settings,
          magic_dark_mode_pages: updatedPages,
        },
      },
    });

    if (response && response.data) {
      appStore.updateState(
        'uixpress_settings',
        response.data.uixpress_settings
      );
      notify({
        message: __('Magic dark mode disabled for this page', 'uixpress'),
        type: 'success',
      });

      // Dispatch settings update event
      document.dispatchEvent(
        new CustomEvent('uipx-settings-update', {
          detail: { magic_dark_mode_pages: updatedPages },
        })
      );
    }
  } catch (error) {
    notify({
      message: __('Failed to disable magic dark mode', 'uixpress'),
      type: 'error',
    });
    console.error('Error disabling magic dark mode:', error);
  }
};

// Define default quick actions
const defaultQuickActions = [
  {
    id: 'new-post',
    name: __('New Post', 'uixpress'),
    icon: 'add',
    action: () => newPost(),
  },
  {
    id: 'new-page',
    name: __('New Page', 'uixpress'),
    icon: 'add',
    action: () => newPage(),
  },
  {
    id: 'view-dashboard',
    name: __('View dashboard', 'uixpress'),
    icon: 'equalizer',
    action: () => (window.location.href = `${appStore.state.adminUrl}`),
  },
  {
    id: 'view-site',
    name: __('View site', 'uixpress'),
    icon: 'home',
    action: () => (window.location.href = `${appStore.state.siteURL}`),
  },
  {
    id: 'site-settings',
    name: __('Site settings', 'uixpress'),
    icon: 'tune',
    action: () =>
      (window.location.href = `${appStore.state.adminUrl}options-general.php`),
  },
  {
    id: 'dark-mode',
    name: __('Dark mode', 'uixpress'),
    icon: 'dark_mode',
    action: () => setColorScheme('dark'),
    hidden: false,
  },
  {
    id: 'light-mode',
    name: __('Light mode', 'uixpress'),
    icon: 'light_mode',
    action: () => setColorScheme('light'),
    hidden: false,
  },
  {
    id: 'enable-magic-dark-mode',
    name: __('Enable magic dark mode for this page', 'uixpress'),
    icon: 'dark_mode',
    action: () => enableMagicDarkModeForCurrentPage(),
    hidden: true, // Will be shown/hidden dynamically
  },
  {
    id: 'disable-magic-dark-mode',
    name: __('Disable magic dark mode for this page', 'uixpress'),
    icon: 'light_mode',
    action: () => disableMagicDarkModeForCurrentPage(),
    hidden: true, // Will be shown/hidden dynamically
  },
];

// Initialize quick actions ref - will be populated via filter hook
const quickActions = ref([]);

/**
 * Parses command syntax from search query
 * Supports: >post, >page, @user, #category, !action, ?help
 * @param {string} query - The search query
 * @returns {Object} Parsed query with filter and cleaned query
 */
const parseCommandSyntax = (query) => {
  if (!query) return { filter: null, cleanQuery: '' };

  const trimmed = query.trim();
  const commandPatterns = {
    '>post': 'post',
    '>page': 'page',
    '@user': 'user',
    '#category': 'category',
    '!action': 'quickActions',
    '?help': 'help',
  };

  // Check for command prefix
  for (const [prefix, filter] of Object.entries(commandPatterns)) {
    if (trimmed.toLowerCase().startsWith(prefix.toLowerCase())) {
      return {
        filter,
        cleanQuery: trimmed.substring(prefix.length).trim(),
      };
    }
  }

  // Check for custom post type filters (>custom-type)
  const customTypeMatch = trimmed.match(/^>(\w+)\s+(.+)$/i);
  if (customTypeMatch) {
    return {
      filter: customTypeMatch[1],
      cleanQuery: customTypeMatch[2],
    };
  }

  return { filter: null, cleanQuery: trimmed };
};

/**
 * Formats a date to relative time (e.g., "2 days ago")
 * @param {string} dateString - ISO date string
 * @returns {string} Formatted relative date
 */
const formatRelativeDate = (dateString) => {
  if (!dateString) return '';

  const date = new Date(dateString);
  const now = new Date();
  const diffMs = now - date;
  const diffSecs = Math.floor(diffMs / 1000);
  const diffMins = Math.floor(diffSecs / 60);
  const diffHours = Math.floor(diffMins / 60);
  const diffDays = Math.floor(diffHours / 24);

  if (diffSecs < 60) return __('Just now', 'uixpress');
  if (diffMins < 60)
    return `${diffMins} ${
      diffMins === 1 ? __('minute', 'uixpress') : __('minutes', 'uixpress')
    } ${__('ago', 'uixpress')}`;
  if (diffHours < 24)
    return `${diffHours} ${
      diffHours === 1 ? __('hour', 'uixpress') : __('hours', 'uixpress')
    } ${__('ago', 'uixpress')}`;
  if (diffDays < 7)
    return `${diffDays} ${
      diffDays === 1 ? __('day', 'uixpress') : __('days', 'uixpress')
    } ${__('ago', 'uixpress')}`;

  // Format as date if older than a week
  return date.toLocaleDateString();
};

/**
 * Gets post status badge color class
 * @param {string} status - Post status
 * @returns {string} CSS class for badge
 */
const getStatusBadgeClass = (status) => {
  const statusMap = {
    publish:
      'bg-green-100 dark:bg-green-900/30 text-green-700 dark:text-green-400',
    draft:
      'bg-yellow-100 dark:bg-yellow-900/30 text-yellow-700 dark:text-yellow-400',
    pending:
      'bg-orange-100 dark:bg-orange-900/30 text-orange-700 dark:text-orange-400',
    private: 'bg-zinc-100 dark:bg-zinc-800 text-zinc-700 dark:text-zinc-400',
    future:
      'bg-indigo-100 dark:bg-indigo-900/30 text-indigo-700 dark:text-indigo-400',
  };
  return statusMap[status] || statusMap.draft;
};

/**
 * Gets post status label
 * @param {string} status - Post status
 * @returns {string} Translated status label
 */
const getStatusLabel = (status) => {
  const statusMap = {
    publish: __('Published', 'uixpress'),
    draft: __('Draft', 'uixpress'),
    pending: __('Pending', 'uixpress'),
    private: __('Private', 'uixpress'),
    future: __('Scheduled', 'uixpress'),
  };
  return statusMap[status] || status;
};

// Parse command syntax from search query
const parsedQuery = computed(() => parseCommandSyntax(searchQuery.value));
const commandFilter = computed(() => parsedQuery.value.filter);

// Filter quick actions based on search query
const filteredQuickActions = computed(() => {
  const query = parsedQuery.value.cleanQuery.toLowerCase();

  // If command filter is set to quickActions or help, show all
  if (
    commandFilter.value === 'quickActions' ||
    commandFilter.value === 'help'
  ) {
    return quickActions.value.filter((action) => !action.hidden);
  }

  // If command filter excludes quickActions, hide them
  if (commandFilter.value && commandFilter.value !== 'quickActions') {
    return [];
  }

  if (!query) return quickActions.value.filter((action) => !action.hidden);
  return quickActions.value.filter(
    (action) => action.name.toLowerCase().includes(query) && !action.hidden
  );
});

/**
 * Filters search results based on command filter
 */
const filteredSearchResults = computed(() => {
  const results = { ...searchResults.value };

  // Apply command filter if set
  if (commandFilter.value) {
    if (commandFilter.value === 'quickActions') {
      return {};
    }
    if (commandFilter.value === 'help') {
      return {};
    }

    // Filter to only show matching category
    const filtered = {};
    if (results[commandFilter.value]) {
      filtered[commandFilter.value] = results[commandFilter.value];
    }
    return filtered;
  }

  return results;
});

const flattenedResults = computed(() => {
  const results = Object.entries(filteredSearchResults.value).flatMap(
    ([category, results]) => results.map((result) => ({ ...result, category }))
  );

  // Add recently accessed and frequently used when no query
  const allResults = [
    ...filteredQuickActions.value.map((action) => ({
      ...action,
      category: 'quickActions',
    })),
    ...results,
  ];

  // Add recently accessed if no query or query matches
  if (
    !parsedQuery.value.cleanQuery ||
    parsedQuery.value.cleanQuery.length === 0
  ) {
    const recent = getFilteredRecentlyAccessed(
      parsedQuery.value.cleanQuery
    ).map((item) => ({
      ...item,
      category: item.category || 'recent',
      isRecent: true,
    }));
    allResults.push(...recent);
  }

  // Add frequently used if no query or query matches
  if (
    !parsedQuery.value.cleanQuery ||
    parsedQuery.value.cleanQuery.length === 0
  ) {
    const frequent = getFilteredFrequentlyUsed(
      parsedQuery.value.cleanQuery
    ).map((item) => ({
      ...item,
      category: item.category || 'frequent',
      isFrequent: true,
    }));
    allResults.push(...frequent);
  }

  return allResults;
});

const returnCategoryName = (slug) => {
  const categories = returnEndPoints.value;
  const category = categories.find((cat) => cat.slug == slug);

  return category ? category.name : slug;
};

const returnAdminMenuLinks = computed(() => {
  let formatted = [];

  if (!Array.isArray(appStore.state.adminMenu)) return [];

  for (let parent of appStore.state.adminMenu) {
    // Not interested in seps
    if (parent.type == 'separator') continue;
    if (parent?.settings?.hidden) continue;

    if (
      inSearch(
        searchQuery.value,
        parent?.settings?.name || parent.name,
        parent.url
      )
    ) {
      formatted.push({
        url: parent.url,
        name: parent?.settings?.name || parent.name,
        id: `${parent.url}-${parent.name}`,
      });
    }

    if (!Array.isArray(parent.submenu)) continue;

    // Loop submenu
    for (let subitem of parent.submenu) {
      // Not interested in seps
      if (subitem.type == 'separator') continue;
      if (subitem?.settings?.hidden) continue;

      // Not in search
      if (
        !inSearch(
          searchQuery.value,
          subitem.name || subitem?.settings?.name,
          subitem.url
        )
      )
        continue;

      formatted.push({
        url: subitem.url,
        name: `<span class="text-zinc-400 dark:text-zinc-400">${
          parent?.settings?.name || parent.name
        } ></span> ${subitem?.settings?.name || subitem.name}`,
        id: `${subitem.url}-${subitem.name}`,
      });
    }
  }

  return formatted;
});

/**
 * Checks if user has seen onboarding
 */
const checkOnboardingStatus = () => {
  const stored = localStorage.getItem('uixp-search-onboarding-seen');
  hasSeenOnboarding.value = stored === 'true';
};

/**
 * Marks onboarding as seen
 */
const markOnboardingSeen = () => {
  localStorage.setItem('uixp-search-onboarding-seen', 'true');
  hasSeenOnboarding.value = true;
  showOnboarding.value = false;
};

/**
 * Shows onboarding tutorial
 */
const showOnboardingTutorial = () => {
  showOnboarding.value = true;
};

const closeModal = () => {
  showModal.value = false;
  searchQuery.value = '';
  searchResults.value = {};
  activeResultIndex.value = 0;
  showHistoryDropdown.value = false;
  showOnboarding.value = false;
};

const returnEndPoints = computed(() => {
  const search_post_types = appStore.state.uixpress_settings.search_post_types;

  let endpoints = [
    {
      name: 'posts',
      slug: 'post',
      endpoint: 'wp/v2/posts',
      searchParam: 'search',
    },
    {
      name: 'pages',
      slug: 'page',
      endpoint: 'wp/v2/pages',
      searchParam: 'search',
    },
  ];

  if (Array.isArray(search_post_types)) {
    if (search_post_types.length > 0) {
      endpoints.splice(0, 2);

      const formatted = search_post_types.map((item) => ({
        ...item,
        endpoint: item.rest_base,
        searchParam: 'search',
      }));

      endpoints = [...formatted, ...endpoints];
    }
  }

  endpoints.push({
    name: 'categories',
    slug: 'category',
    endpoint: 'wp/v2/categories',
    searchParam: 'search',
  });

  if (appStore.state.canManageOptions) {
    endpoints.push({
      name: 'users',
      slug: 'user',
      endpoint: 'wp/v2/users',
      searchParam: 'search',
    });
  }

  return endpoints;
});

const performSearch = async () => {
  const cleanQuery = parsedQuery.value.cleanQuery;

  if (cleanQuery.trim() === '') {
    searchResults.value = {};
    return;
  }

  if (cleanQuery.length < 1) return;

  // Add to search history
  addToHistory(cleanQuery);

  isLoading.value = true;
  try {
    // Filter endpoints based on command filter
    let endpointsToSearch = returnEndPoints.value;

    if (
      parsedQuery.value.filter &&
      parsedQuery.value.filter !== 'quickActions' &&
      parsedQuery.value.filter !== 'help'
    ) {
      endpointsToSearch = returnEndPoints.value.filter(
        (ep) => ep.slug === parsedQuery.value.filter
      );
    }

    const searchPromises = endpointsToSearch.map(
      ({ name, endpoint, searchParam, slug }) =>
        lmnFetch({
          endpoint,
          params: {
            [searchParam]: cleanQuery,
            per_page: 5,
            context: 'view',
            author: appStore.state.canManageOptions
              ? ''
              : appStore.state.userID,
            _embed: true, // Include embedded data for featured media and author
          },
        }).then((response) => ({ slug, data: response.data }))
    );

    const results = await Promise.all(searchPromises);

    results.forEach(({ slug, data }) => {
      searchResults.value[slug] = data;
    });
  } catch (error) {
    console.error('Error performing search:', error);
  } finally {
    isLoading.value = false;
  }
};

const debounce = (fn, delay) => {
  let timeoutId;
  return (...args) => {
    clearTimeout(timeoutId);
    timeoutId = setTimeout(() => fn(...args), delay);
  };
};

const openModal = (evt) => {
  document.activeElement.blur();

  searchmodal.value.show(evt);
  activeResultIndex.value = 0;

  // Check onboarding status on first open
  if (!hasSeenOnboarding.value) {
    checkOnboardingStatus();
    if (!hasSeenOnboarding.value) {
      // Show onboarding after a brief delay
      setTimeout(() => {
        showOnboarding.value = true;
      }, 300);
    }
  }
};

const debouncedSearch = debounce(performSearch, 300);

watch(searchQuery, (newValue, oldValue) => {
  if (newValue !== oldValue) {
    debouncedSearch();
    activeResultIndex.value = 0;
    showHistoryDropdown.value = false;
  }
});

// Show history dropdown when input is focused and query is empty
watchEffect(() => {
  if (searchInput.value && searchInput.value === document.activeElement) {
    if (!searchQuery.value || searchQuery.value.trim() === '') {
      showHistoryDropdown.value = true;
    }
  }
});

const handleKeyDown = (event) => {
  if ((event.metaKey || event.ctrlKey) && event.key === 'k') {
    event.preventDefault();
    openModal();
  }

  if (event.key === 'Escape') {
    searchmodal.value.close();
  }
};

const handleArrowDown = (event) => {
  event.preventDefault();
  activeResultIndex.value =
    (activeResultIndex.value + 1) % flattenedResults.value.length;
};

const handleArrowUp = (event) => {
  event.preventDefault();
  activeResultIndex.value =
    (activeResultIndex.value - 1 + flattenedResults.value.length) %
    flattenedResults.value.length;
};

const handleKeyEnter = (event) => {
  event.preventDefault();
  const activeResult = flattenedResults.value[activeResultIndex.value];
  if (activeResult) {
    if (activeResult.category === 'quickActions') {
      activeResult.action();
    } else {
      // Track access before navigating
      trackAccess({
        id: activeResult.id || activeResult.url,
        category: activeResult.category,
        name:
          activeResult.name ||
          activeResult.title?.rendered ||
          activeResult.email,
        url: returnItemLink(activeResult.category, activeResult),
        metadata: activeResult,
      });

      window.location.href = returnItemLink(
        activeResult.category,
        activeResult
      );
    }
    searchmodal.value.close();
  }
};

/**
 * Handles item click and tracks access
 * @param {Object} item - The clicked item
 * @param {string} category - Item category
 */
const handleItemClick = (item, category) => {
  if (category !== 'quickActions') {
    trackAccess({
      id: item.id || item.url,
      category: category,
      name: item.name || item.title?.rendered || item.email,
      url: returnItemLink(category, item),
      metadata: item,
    });
  }
};

const returnItemLink = (category, item) => {
  if (category == 'menu') return item.url;
  if (category == 'post' || category == 'page')
    return `${appStore.state.adminUrl}post.php?post=${item.id}&action=edit`;
  if (category == 'user')
    return `${appStore.state.adminUrl}user-edit.php?user_id=${item.id}`;
  return `${appStore.state.adminUrl}post.php?post=${item.id}&action=edit`;
};

/**
 * Checks if search results are empty
 */
const hasNoResults = computed(() => {
  if (commandFilter.value === 'help') return false;
  if (showOnboarding.value) return false;

  const hasQuickActions = filteredQuickActions.value.length > 0;
  const hasRecent = getFilteredRecentlyAccessed().length > 0;
  const hasFrequent = getFilteredFrequentlyUsed().length > 0;
  const hasResults = Object.keys(filteredSearchResults.value).length > 0;
  const hasHistory =
    showHistoryDropdown.value && searchHistory.value.length > 0;

  // If we have a query but no results
  if (parsedQuery.value.cleanQuery && parsedQuery.value.cleanQuery.length > 0) {
    return !hasResults && !hasQuickActions;
  }

  // Empty state when no query
  return !hasQuickActions && !hasRecent && !hasFrequent && !hasHistory;
});

/**
 * Example searches for onboarding
 */
const exampleSearches = [
  {
    query: '>post',
    label: __('Search posts', 'uixpress'),
    icon: 'description',
  },
  { query: '@user', label: __('Find users', 'uixpress'), icon: 'person' },
  { query: '!action', label: __('Quick actions', 'uixpress'), icon: 'bolt' },
  { query: '?help', label: __('View commands', 'uixpress'), icon: 'help' },
];

/**
 * Handles example search click
 * @param {string} query - The search query to set
 */
const handleExampleSearch = (query) => {
  searchQuery.value = query;
  if (showOnboarding.value) {
    markOnboardingSeen();
  }
};

/**
 * Handles input blur event with delayed dropdown hiding
 */
const handleInputBlur = () => {
  setTimeout(() => {
    showHistoryDropdown.value = false;
  }, 200);
};

onMounted(async () => {
  window.addEventListener('keydown', handleKeyDown);
  checkOnboardingStatus();

  // Dispatch event for plugins to register quick actions
  const event = new CustomEvent('uixpress/search/ready');
  document.dispatchEvent(event);

  await nextTick();

  // Register quick actions via filter hook - allows plugins to add/remove actions
  let actionsToRegister = [...defaultQuickActions];

  // Remove site settings if user can't manage options
  if (!appStore.state.canManageOptions) {
    actionsToRegister = actionsToRegister.filter(
      (item) => item.id !== 'site-settings'
    );
  }

  // Apply filter hook to allow plugins to register/deregister actions
  quickActions.value = await applyFilters(
    'uixpress/search/quickActions/register',
    actionsToRegister
  );
});

onUnmounted(() => {
  window.removeEventListener('keydown', handleKeyDown);
});

watchEffect(() => {
  if (searchInput.value) {
    searchInput.value.focus();
  }
});

watchEffect(() => {
  const darkItem = quickActions.value.find((item) => item.id == 'dark-mode');
  const lightItem = quickActions.value.find((item) => item.id == 'light-mode');
  const enableMagicDarkItem = quickActions.value.find(
    (item) => item.id == 'enable-magic-dark-mode'
  );
  const disableMagicDarkItem = quickActions.value.find(
    (item) => item.id == 'disable-magic-dark-mode'
  );

  // Skip if items not found (not registered or filtered out)
  if (!darkItem || !lightItem) return;

  // Global theme is set so remove switchers
  if (globalOverride.value) {
    darkItem.hidden = true;
    lightItem.hidden = true;
    if (enableMagicDarkItem) enableMagicDarkItem.hidden = true;
    if (disableMagicDarkItem) disableMagicDarkItem.hidden = true;
    return;
  }

  if (prefersDark.value) {
    darkItem.hidden = true;
    lightItem.hidden = false;
  } else {
    darkItem.hidden = false;
    lightItem.hidden = true;
  }

  // Show/hide magic dark mode actions based on current page status
  if (enableMagicDarkItem && disableMagicDarkItem) {
    const isInList = isCurrentPageInMagicDarkModeList();
    // Only show these actions when in dark mode
    if (prefersDark.value) {
      enableMagicDarkItem.hidden = isInList; // Hide if already enabled
      disableMagicDarkItem.hidden = !isInList; // Hide if not enabled
    } else {
      // Hide both when not in dark mode
      enableMagicDarkItem.hidden = true;
      disableMagicDarkItem.hidden = true;
    }
  }
});

watch(
  () => parsedQuery.value.cleanQuery,
  () => {
    searchResults.value.menu = returnAdminMenuLinks.value;
  }
);
</script>

