                          "
                          class="w-8 aspect-square bg-zinc-200 dark:bg-zinc-700 rounded flex place-content-center items-center justify-center shrink-0"
                        >
                          <AppIcon
                            :icon="
                              category === 'category'
                                ? 'folder'
                                : category === 'menu'
                                ? 'menu'
                                : 'description'
                            "
                            class="text-zinc-400 text-sm"
                          />
                        </div>

                        <div class="grow min-w-0 flex flex-col gap-1">
                          <div class="flex items-center gap-2">
                            <span
                              class="truncate font-medium text-zinc-900 dark:text-zinc-100"
                              v-html="
                                result.name ||
                                result.title?.rendered ||
                                result.email
                              "
                            ></span>
                            <!-- Status Badge -->
                            <span
                              v-if="
                                result.status &&
                                (category === 'post' || category === 'page')
                              "
                              :class="[
                                'px-1.5 py-0.5 rounded text-xs font-medium',
                                getStatusBadgeClass(result.status),
                              ]"
                            >
                              {{ getStatusLabel(result.status) }}
                            </span>
                          </div>
                          <!-- Metadata -->
                          <div
                            class="flex items-center gap-2 text-xs text-zinc-500 dark:text-zinc-400"
                          >
                            <span
                              v-if="
                                result.date &&
                                (category === 'post' || category === 'page')
                              "
                              >{{ formatRelativeDate(result.date) }}</span
                            >
                            <span
                              v-if="
                                result.author &&
                                result._embedded &&
                                result._embedded.author &&
                                result._embedded.author[0]
                              "
                              class="flex items-center gap-1"
                            >
                              <span></span>
                              <span>{{ result._embedded.author[0].name }}</span>
                            </span>
                            <span
                              v-if="category === 'user' && result.roles"
                              class="flex items-center gap-1"
                            >
                              <span></span>
                              <span>{{ result.roles.join(', ') }}</span>
                            </span>
                          </div>
                        </div>
                        <AppIcon
                          icon="chevron_right"
                          class="text-zinc-400 shrink-0"
                        />
                      </a>
                    </li>
                  </ul>
                </div>
              </template>
            </div>
          </Transition>
        </div>
        <div
          class="absolute bottom-0 left-0 right-0 h-10 bg-gradient-to-t from-white dark:from-zinc-900 to-transparent pointer-events-none"
          v-if="!isLoading && flattenedResults.length > 20"
        />
      </div>

      <div class="flex flex-row gap-6 items-center text-zinc-500">
        <!--Arrows-->
        <div class="flex flex-row gap-2 items-center">
          <span
            class="p-1 border border-zinc-200 dark:border-zinc-700 rounded-lg"
          >
            <AppIcon icon="arrow_up" />
          </span>
          <span
            class="p-1 border border-zinc-200 dark:border-zinc-700 rounded-lg"
          >
            <AppIcon icon="arrow_down" />
          </span>

          <span class="text-sm">{{ __('to navigate', 'uixpress') }}</span>
        </div>

        <!--Arrows-->
        <div class="flex flex-row gap-2 items-center">
          <span
            class="p-1 border border-zinc-200 dark:border-zinc-700 rounded-lg"
          >
            <AppIcon icon="return" />
          </span>

          <span class="text-sm">{{ __('to select', 'uixpress') }}</span>
        </div>

        <!--Arrows-->
        <div class="flex flex-row gap-2 items-center">
          <span
            class="p-1 border border-zinc-200 dark:border-zinc-700 rounded-lg text-xs"
            >Esc</span
          >

          <span class="text-sm">{{ __('to close', 'uixpress') }}</span>
        </div>
      </div>
    </div>
  </Modal>
</template>

