<script setup>
import { ref, computed, onMounted } from 'vue';
import { encodeToHash } from '@/assets/js/functions/encodeToHash.js';

// Store
import { useAppStore } from '@/store/app/app.js';
const appStore = useAppStore();

// Comps
import AppIcon from '@/components/utility/icons/index.vue';

// Funcs
import { lmnFetch } from '@/assets/js/functions/lmnFetch.js';
import {
  setupColorScheme,
  useColorScheme,
} from '@/assets/js/functions/useColorScheme.js';

const { prefersDark, setColorScheme, colorScheme, userPreference } =
  useColorScheme();

// Refs
const gravatar = ref(false);
const showUserDropdown = ref(false);
const userDropdownRef = ref(null);
const userSectionRef = ref(null);

// User dropdown menu items
const userMenuItems = computed(() => {
  const tabs = [
    {
      id: 'profile',
      name: __('Edit Profile', 'uixpress'),
      icon: 'person',
      url: `${appStore.state.adminUrl}profile.php`,
      divider: false,
    },
    {
      id: 'view-site',
      name: __('Visit site', 'uixpress'),
      icon: 'home',
      url: `${appStore.state.siteURL}`,
      divider: false,
    },
    {
      id: 'site-settings',
      name: __('Site Settings', 'uixpress'),
      icon: 'settings',
      url: `${appStore.state.adminUrl}options-general.php`,
      divider: false,
    },
    {
      id: 'logout',
      name: __('Sign Out', 'uixpress'),
      icon: 'logout',
      url: ``,
      action: logoutUser,
      divider: false,
      destructive: true,
    },
  ];

  if (!appStore.state.userRoles.includes('administrator')) {
    tabs.splice(2, 1);
  }

  return tabs;
});

// Props
const props = defineProps({
  openSearch: {
    type: Function,
    required: true,
  },
});

/**
 * Handles user menu action clicks
 * @param {Object} item - The menu item that was clicked
 */
const handleUserMenuAction = (item) => {
  if (item.action) {
    item.action();
  } else if (item.url) {
    window.location.href = item.url;
  }
  showUserDropdown.value = false;
};

/**
 * Logs out the current user
 */
const logoutUser = async () => {
  const args = { endpoint: 'uixpress/v1/logout', type: 'POST', params: {} };
  const response = await lmnFetch(args);

  if (response) {
    window.location.href = response.data.redirect_url;
  }
};

/**
 * Fetches user gravatar
 */
// Methods
const setGravatar = async () => {
  const hashHex = await encodeToHash(appStore.state.userEmail);
  gravatar.value = `https://gravatar.com/avatar/${hashHex}?d=blank`;
};

onMounted(() => {
  setGravatar();
});
</script>

<template>
  <!-- USER DETAILS with Dropdown -->
  <div
    class="flex flex-row items-center gap-3 shrink relative"
    @mouseenter="showUserDropdown = true"
    @mouseleave="showUserDropdown = false"
  >
    <!-- User Dropdown -->
    <Transition
      enter-active-class="transition duration-200 ease-out"
      enter-from-class="transform scale-95 opacity-0"
      enter-to-class="transform scale-100 opacity-100"
      leave-active-class="transition duration-150 ease-in"
      leave-from-class="transform scale-100 opacity-100"
      leave-to-class="transform scale-95 opacity-0"
    >
      <div
        v-if="showUserDropdown"
        class="pb-2 absolute bottom-full left-0 z-50"
      >
        <div
          ref="userDropdownRef"
          class="bg-white dark:bg-zinc-900 rounded-lg shadow-lg border border-zinc-200 dark:border-zinc-800 min-w-[240px]"
        >
          <!-- User info header -->
          <div class="px-4 py-3 border-b border-zinc-100 dark:border-zinc-800">
            <div class="flex items-center gap-3">
              <div
                class="w-8 h-8 bg-zinc-700 dark:bg-zinc-600 text-white rounded-full font-medium flex items-center justify-center relative overflow-hidden"
              >
                <span
                  class="lowercase text-sm"
                  style="top: calc(((-1em / 1.5) + 1ex) / 2)"
                  >{{ appStore.state.userName.charAt(0) }}</span
                >
                <img
                  v-if="gravatar"
                  :src="gravatar"
                  class="absolute w-full h-full object-cover"
                />
              </div>
              <div class="flex flex-col leading-tight grow min-w-0">
                <span
                  class="font-medium text-zinc-900 dark:text-zinc-100 truncate text-sm"
                  >{{ appStore.state.userName }}</span
                >
                <span
                  class="text-xs text-zinc-500 dark:text-zinc-400 truncate"
                  >{{ appStore.state.userEmail }}</span
                >
              </div>
            </div>
          </div>

          <!-- Menu items -->
          <div class="py-1">
            <template v-for="item in userMenuItems" :key="item.id">
              <button
                @click="handleUserMenuAction(item)"
                class="w-full px-4 py-2.5 text-left flex items-center gap-3 hover:bg-zinc-50 dark:hover:bg-zinc-800 transition-colors"
                :class="
                  item.destructive
                    ? 'text-red-600 dark:text-red-400 hover:bg-red-50 dark:hover:bg-red-900/20'
                    : 'text-zinc-700 dark:text-zinc-300'
                "
              >
                <AppIcon :icon="item.icon" class="text-sm opacity-70" />
                <span class="text-sm">{{ item.name }}</span>
              </button>
              <div
                v-if="item.divider"
                class="h-px bg-zinc-100 dark:bg-zinc-800 my-1 mx-4"
              ></div>
            </template>
          </div>

          <!-- Quick actions footer -->
          <div class="px-4 py-3 border-t border-zinc-100 dark:border-zinc-800">
            <div class="space-y-3">
              <!-- Theme selector -->
              <div class="flex items-center justify-between">
                <span
                  class="text-xs font-medium text-zinc-600 dark:text-zinc-300"
                  >{{ __('Theme', 'uixpress') }}</span
                >
                <div class="flex bg-zinc-100 dark:bg-zinc-800 rounded-lg p-0.5">
                  <button
                    @click="setColorScheme('light')"
                    class="px-2 py-1 text-xs rounded-md transition-colors"
                    :class="
                      userPreference === 'light'
                        ? 'bg-white dark:bg-zinc-700 text-zinc-900 dark:text-zinc-100 shadow-sm'
                        : 'text-zinc-500 dark:text-zinc-400 hover:text-zinc-700 dark:hover:text-zinc-200'
                    "
                    :title="__('Light Mode', 'uixpress')"
                  >
                    <AppIcon icon="light_mode" class="text-sm" />
                  </button>
                  <button
                    @click="setColorScheme('dark')"
                    class="px-2 py-1 text-xs rounded-md transition-colors"
                    :class="
                      userPreference === 'dark'
                        ? 'bg-white dark:bg-zinc-700 text-zinc-900 dark:text-zinc-100 shadow-sm'
                        : 'text-zinc-500 dark:text-zinc-400 hover:text-zinc-700 dark:hover:text-zinc-200'
                    "
                    :title="__('Dark Mode', 'uixpress')"
                  >
                    <AppIcon icon="dark_mode" class="text-sm" />
                  </button>
                  <button
                    @click="setColorScheme('system')"
                    class="px-2 py-1 text-xs rounded-md transition-colors"
                    :class="
                      userPreference === 'system'
                        ? 'bg-white dark:bg-zinc-700 text-zinc-900 dark:text-zinc-100 shadow-sm'
                        : 'text-zinc-500 dark:text-zinc-400 hover:text-zinc-700 dark:hover:text-zinc-200'
                    "
                    :title="__('System', 'uixpress')"
                  >
                    <AppIcon icon="settings" class="text-sm" />
                  </button>
                </div>
              </div>

              <!-- Search button -->
              <div class="flex items-center justify-between">
                <span class="text-xs text-zinc-500 dark:text-zinc-400">{{
                  __('Quick Actions', 'uixpress')
                }}</span>
                <button
                  @click="
                    () => {
                      openSearch();
                      showUserDropdown = false;
                    }
                  "
                  class="p-1.5 rounded hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-colors"
                  :title="__('Focus Search', 'uixpress')"
                >
                  <AppIcon
                    icon="search"
                    class="text-sm text-zinc-500 dark:text-zinc-400"
                  />
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Transition>

    <div
      class="p-2 bg-white dark:bg-zinc-800/40 rounded-xl flex flex-row items-center gap-3 w-full border border-zinc-200/40 dark:border-zinc-700/30"
    >
      <div
        ref="userSectionRef"
        class="w-8 aspect-square bg-zinc-700 dark:bg-zinc-600 text-white rounded-full font-medium flex items-center justify-center shrink-0 relative overflow-hidden cursor-pointer hover:bg-zinc-600 dark:hover:bg-zinc-500 transition-colors"
      >
        <span
          class="lowercase relative text-sm"
          style="top: calc(((-1em / 1.5) + 1ex) / 2)"
          >{{ appStore.state.userName.charAt(0) }}</span
        >
        <img
          v-if="gravatar"
          :src="gravatar"
          class="absolute w-full h-full object-cover"
        />
      </div>
      <div
        class="flex flex-col leading-tight grow min-w-0 cursor-pointer hover:bg-zinc-50 dark:hover:bg-zinc-800/50 rounded-lg p-2 -m-2 transition-colors"
      >
        <div class="flex items-center justify-between">
          <span
            class="truncate font-medium text-zinc-900 dark:text-zinc-100 text-sm"
            >{{ appStore.state.userName }}</span
          >
          <AppIcon
            :icon="showUserDropdown ? 'keyboard_arrow_up' : 'expand_more'"
            class="text-sm text-zinc-400 transition-transform"
            :class="showUserDropdown ? 'rotate-0' : 'rotate-0'"
          />
        </div>
        <span class="text-xs text-zinc-500 dark:text-zinc-400 truncate">{{
          appStore.state.userEmail
        }}</span>
      </div>
    </div>
  </div>
</template>
