<script setup>
import { defineProps, computed } from 'vue';

const props = defineProps(['link', 'isActive', 'isFocused']);

/**
 * Computed property to ensure the URL is properly formatted
 * Decodes the URL if it's already encoded to prevent double-encoding
 * @returns {string} The properly formatted URL
 */
const formattedUrl = computed(() => {
  if (!props.link?.url) return '';
  
  const url = props.link.url;
  
  // Check if URL contains encoded characters that shouldn't be encoded
  // (e.g., %2F for / in query parameters)
  // Only decode if we detect encoded characters
  if (url.includes('%')) {
    try {
      // Decode the URL to ensure it's in raw format
      // This prevents double-encoding issues where URLs like
      // admin.php?page=wc-admin&path=/analytics/overview
      // get rendered as admin.php?page=wc-admin&path=%2Fanalytics%2Foverview
      const decoded = decodeURIComponent(url);
      // Only use decoded version if it's different and valid
      return decoded;
    } catch (e) {
      // If decoding fails (invalid encoding), return original
      return url;
    }
  }
  
  // No encoded characters detected, return as-is
  return url;
});
</script>

<template>
  <a
    :href="formattedUrl"
    :id="link.id"
    class="flex flex-row rounded-lg items-center cursor-pointer gap-1 group transition-all relative border border-transparent"
    :class="
      isActive || isFocused
        ? 'text-zinc-900 dark:text-zinc-100 active bg-zinc-100 dark:bg-zinc-800/40 active'
        : 'hover:bg-zinc-100 hover:dark:bg-zinc-800/60'
    "
    :target="link.settings?.open_new ? '_BLANK' : ''"
  >
    <slot />
  </a>
</template>
