<script setup>
import {
  ref,
  computed,
  watchEffect,
  defineProps,
  watch,
  nextTick,
  onMounted,
  onUnmounted,
  provide,
} from 'vue';

// Configure veaury for React 19 support
import { setVeauryOptions } from 'veaury';
import { createRoot } from 'react-dom/client';
setVeauryOptions({
  react: {
    createRoot,
  },
});

// Funcs
import { lmnFetch } from '@/assets/js/functions/lmnFetch.js';
import { processMenu } from './src/processMenu.js';
import { returnOriginalLinkAttribute } from './src/returnOriginalLinkAttribute.js';
import { returnIconOverrides } from './src/returnIconOverrides.js';
import { returnDashIconClasses } from './src/returnDashIconClasses.js';
import { isCurrentLocationMatch } from './src/isCurrentLocationMatch.js';
import { applyFilters } from '@/assets/js/functions/HooksSystem.js';
import {
  setupColorScheme,
  useColorScheme,
} from '@/assets/js/functions/useColorScheme.js';
import { VueDraggableNext } from 'vue-draggable-next';
const { prefersDark, setColorScheme } = useColorScheme();

// Comps
import AppIcon from '@/components/utility/icons/index.vue';
import AppButton from '@/components/utility/app-button/index.vue';
import ResizerHandler from '@/components/app/panel-resizer/index.vue';
import SubMenu from './src/submenu.vue';
import MenuIcon from './src/menu-icon.vue';
import MenuItemName from './src/menu-item-name.vue';
import MenuItemLink from './src/menu-item-link.vue';
import SubMenuItem from './src/submenu-item.vue';
import AdminNotices from '@/components/app/admin-notices/index.vue';
import UserDetails from './src/user-details.vue';
import ComponentRender from '../component-render.vue';
import RemoteSiteSelect from '@/components/utility/remote-site-select/index.vue';

// Store
import { useAppStore } from '@/store/app/app.js';
const appStore = useAppStore();

// Refs
import { loading, menu, dashIconsList, OGmenu } from './src/constants.js';
const menupanel = ref(null);
const panelWidth = ref(240);
const menus = ref([]);
const props = defineProps(['mobile']);
const selectedRemoteSite = ref(
  localStorage.getItem('uixpress_selected_site') || 'local'
);

// Favorites
import { useFavorites } from './src/useFavorites.js';
const { favorites, addFavorite, removeFavorite, updateFavorite, isFavorite } =
  useFavorites();

// Provide addFavorite for child components
provide('addFavorite', addFavorite);

// Shortcuts editing
const editingShortcut = ref(null);
const shortcutEditName = ref('');
const shortcutEditUrl = ref('');
const shortcutEditIcon = ref('');

/**
 * Checks if user can see the remote site switcher based on capability
 *
 * @computed
 * @returns {boolean} Whether user can see the switcher
 */
const canSeeRemoteSiteSwitcher = computed(() => {
  const settings = appStore.state?.uixpress_settings;
  const remoteSites = settings?.remote_sites || [];

  // Don't show if no remote sites configured
  if (!Array.isArray(remoteSites) || remoteSites.length === 0) {
    return false;
  }

  // Get capability requirement (defaults to 'manage_options')
  const requiredCapability =
    settings?.remote_site_switcher_capability || 'manage_options';

  // If capability is 'manage_options', use the existing canManageOptions flag
  if (requiredCapability === 'manage_options') {
    return appStore.state.canManageOptions === true;
  }

  // For other capabilities, we'd need to check via REST API or have it passed from backend
  // For now, default to showing if user can manage options (admin)
  // This can be enhanced later to support other capabilities
  return appStore.state.canManageOptions === true;
});

// Sidebar render hooks
const preMenuComponents = ref([]);
const postMenuComponents = ref([]);

// Comps for editing
import AppInput from '@/components/utility/text-input/index.vue';
import IconSelect from '@/components/utility/icon-select/index.vue';

/**
 * Starts editing a shortcut
 */
const startEditShortcut = (shortcut) => {
  editingShortcut.value = shortcut;
  shortcutEditName.value = shortcut.settings?.name || shortcut.name || '';
  shortcutEditUrl.value = shortcut.url || '';
  shortcutEditIcon.value = shortcut.settings?.icon || 'link';
};

/**
 * Saves shortcut edits
 */
const saveShortcutEdit = () => {
  if (!editingShortcut.value) return;

  updateFavorite(editingShortcut.value, {
    url: shortcutEditUrl.value,
    name: shortcutEditName.value,
    settings: {
      ...editingShortcut.value.settings,
      name: shortcutEditName.value,
      icon: shortcutEditIcon.value,
    },
  });

  editingShortcut.value = null;
  shortcutEditName.value = '';
  shortcutEditUrl.value = '';
  shortcutEditIcon.value = '';
};

/**
 * Cancels shortcut editing
 */
const cancelShortcutEdit = () => {
  editingShortcut.value = null;
  shortcutEditName.value = '';
  shortcutEditUrl.value = '';
  shortcutEditIcon.value = '';
};

/**
 * Checks if a remote site is currently active
 * @returns {boolean} True if remote site is active
 */
const isRemoteSiteActive = computed(() => {
  return selectedRemoteSite.value && selectedRemoteSite.value !== 'local';
});

/**
 * Gets the allowed menu IDs for remote sites based on enabled settings
 * @returns {Object} Object with topLevelIds and submenuIds arrays
 */
const getAllowedRemoteMenuIds = () => {
  const settings = appStore.state?.uixpress_settings || {};
  const topLevelIds = [];
  const submenuIds = [];

  // uiXpress top-level menu - always keep it
  topLevelIds.push('uipx-settings');

  // uiXpress submenu items (under uipx-settings)
  // Menu Creator - always available
  submenuIds.push('uipx-menucreator');

  // Activity Log
  if (settings.enable_activity_logger === true) {
    submenuIds.push('uixpress-activity-log');
  }

  // Database Explorer
  if (settings.enable_database_explorer === true) {
    submenuIds.push('uixpress-database-explorer');
  }

  // Admin Notices - always available
  submenuIds.push('uixpress-admin-notices');

  // Role Editor
  if (settings.enable_role_editor === true) {
    submenuIds.push('uipx-role-editor');
  }

  // Settings submenu (the main settings page itself)
  submenuIds.push('uipx-settings');

  // Other top-level uiXpress pages
  // Modern Media Page - WordPress uses "menu-media" as the ID
  if (settings.use_modern_media_page === true) {
    topLevelIds.push('menu-media');
    topLevelIds.push('upload'); // Also check for upload in case
  }

  // Modern Plugins Page
  if (settings.use_modern_plugin_page === true) {
    topLevelIds.push('plugin-manager');
    topLevelIds.push('toplevel_page_plugin-manager'); // WordPress format
  }

  // Modern Dashboard - WordPress uses "menu-dashboard" as the ID
  if (settings.use_custom_dashboard === true) {
    topLevelIds.push('menu-dashboard');
    topLevelIds.push('dashboard'); // Also check for dashboard
  }

  // Modern Users Page - WordPress uses "menu-users" as the ID
  if (settings.use_modern_users_page === true) {
    topLevelIds.push('menu-users');
    topLevelIds.push('users'); // Also check for users
  }

  return { topLevelIds, submenuIds };
};

/**
 * Checks if a menu item ID matches any of the allowed IDs
 * @param {string} itemId - The menu item ID to check
 * @param {Array} allowedIds - Array of allowed menu IDs
 * @returns {boolean} True if the item ID matches an allowed ID
 */
const isAllowedMenuId = (itemId, allowedIds) => {
  if (!itemId) return false;

  // Check exact match first
  if (allowedIds.includes(itemId)) return true;

  // Extract the slug from WordPress menu ID formats:
  // - "toplevel_page_plugin-manager" -> "plugin-manager"
  // - "uixpress_page_uixpress-activity-log" -> "uixpress-activity-log"
  // - "toplevel_page_uipx-settings" -> "uipx-settings"
  const idParts = itemId.split('_');

  // Try last part (most common case)
  const lastPart = idParts[idParts.length - 1];
  if (allowedIds.includes(lastPart)) return true;

  // Try matching any part of the ID
  for (const part of idParts) {
    if (allowedIds.includes(part)) return true;
  }

  // Check if any allowed ID is contained in the item ID
  for (const allowedId of allowedIds) {
    if (itemId.includes(allowedId)) return true;
  }

  return false;
};

/**
 * Checks if a menu item is the uiXpress settings menu
 * @param {string} itemId - The menu item ID to check
 * @returns {boolean} True if it's the uiXpress settings menu
 */
const isUiXpressSettingsMenu = (itemId) => {
  if (!itemId) return false;
  return itemId === 'uipx-settings' || itemId.includes('uipx-settings');
};

/**
 * Computed property that filters menu items based on remote site status.
 * @returns {Array} Filtered array of menu items.
 */
const returnMenuItems = computed(() => {
  if (!Array.isArray(menu.value)) return [];

  let filteredMenu = menu.value.filter((item) => !item.settings?.hidden);

  // If remote site is active, filter to only show allowed uiXpress pages
  if (isRemoteSiteActive.value) {
    const { topLevelIds, submenuIds } = getAllowedRemoteMenuIds();

    filteredMenu = filteredMenu
      .filter((item) => {
        // Only include top-level items that match allowed IDs
        return isAllowedMenuId(item.id, topLevelIds);
      })
      .map((item) => {
        // If it's the uiXpress settings menu, keep submenus but filter them
        if (isUiXpressSettingsMenu(item.id)) {
          const filteredSubmenu = Array.isArray(item.submenu)
            ? item.submenu.filter((subItem) => {
                // Submenu items have IDs like: "toplevel_page_uipx-settings-admin.php?page=uipx-menucreator"
                // We need to match by URL parameter since that's the most reliable way

                // Check URL for page parameter (e.g., "admin.php?page=uipx-menucreator")
                if (subItem.url) {
                  for (const allowedId of submenuIds) {
                    // Match both "page=allowedId" and "&page=allowedId"
                    if (
                      subItem.url.includes(`page=${allowedId}`) ||
                      subItem.url.includes(`&page=${allowedId}`)
                    ) {
                      return true;
                    }
                  }
                }

                // Check sub_id (WordPress native menu ID) - format: "uipx-settings_page_uipx-menucreator"
                if (subItem.sub_id) {
                  if (isAllowedMenuId(subItem.sub_id, submenuIds)) {
                    return true;
                  }
                }

                // Check constructed id (format: "toplevel_page_uipx-settings-admin.php?page=uipx-menucreator")
                if (subItem.id) {
                  if (isAllowedMenuId(subItem.id, submenuIds)) {
                    return true;
                  }
                  // Also check if URL is embedded in the ID
                  if (subItem.url) {
                    for (const allowedId of submenuIds) {
                      if (subItem.id.includes(allowedId)) {
                        return true;
                      }
                    }
                  }
                }

                return false;
              })
            : [];

          // Preserve all properties including active state
          return {
            ...item,
            submenu: filteredSubmenu,
            // Preserve active state so menu can expand
            active: item.active,
          };
        }

        // Remove submenus from all other items when remote site is active
        return {
          ...item,
          submenu: [],
        };
      });
  }

  return filteredMenu;
});

/**
 * Computed property that returns whether the menu is minimised
 */
const isMenuMinified = computed(() => {
  if (props.mobile) return false;
  return appStore.state.menu_minimised;
});

const getMenuForUser = async () => {
  // Ensure the original menu has been parsed before moving on
  await setMenu();
  //setTimeout(setMenu, 1000);

  // Attempt to fetch menu from cache
  const cachedMenu = getMenuFromLocalStorage();
  if (Array.isArray(cachedMenu)) {
    menu.value = cachedMenu;
    filterMenu();
    return;
  } else if (cachedMenu == 'no_menus') {
    menu.value = [...OGmenu.value];
    return;
  }

  setTimeout(getRemoteMenus, 1);
};

const returnLayout = computed(() => {
  return appStore.state.uixpress_settings?.layout;
});

const getRemoteMenus = async () => {
  // Ensure the original menu has been parsed before moving on
  await setMenu();

  // Attempt to fetch menu from cache
  const cachedMenu = getMenuFromLocalStorage();
  if (Array.isArray(cachedMenu)) {
    menu.value = cachedMenu;
    filterMenu();
    return;
  } else if (cachedMenu == 'no_menus') {
    menu.value = [...OGmenu.value];
    return;
  }

  loading.value = true;

  const args = {
    endpoint: 'wp/v2/uipxmenus',
    params: { per_page: 100, status: 'publish' },
  };
  const data = await lmnFetch(args);

  loading.value = false;

  if (!data) {
    menu.value = [...OGmenu.value];
    return;
  }

  menus.value = data.data;

  setActiveMenu();
};

const setActiveMenu = () => {
  // No custom menus so bail
  if (!menus.value.length) {
    menu.value = [...OGmenu.value];
    cacheMenu('no_menus');
    return;
  }

  for (let remoteMenu of menus.value) {
    const activeForEveryone =
      remoteMenu.meta?.menu_settings?.applies_to_everyone;

    // Menu is set to be active for everyone
    if (activeForEveryone && Array.isArray(remoteMenu.meta?.menu_items)) {
      // Do not set empty menus to active to avoid lock out
      if (!remoteMenu.meta?.menu_items.length) continue;

      // Update menu
      menu.value = [...remoteMenu.meta?.menu_items];
      cacheMenu(remoteMenu.meta?.menu_items);
      filterMenu();
      return;
    }

    const userRoles = appStore.state.userRoles;
    const includesRoles = remoteMenu.meta?.menu_settings?.includes
      ? remoteMenu.meta?.menu_settings?.includes.filter(
          (item) => item.type == 'role'
        )
      : [];
    const includesUsers = remoteMenu.meta?.menu_settings?.includes
      ? remoteMenu.meta?.menu_settings?.includes.filter(
          (item) => item.type == 'user'
        )
      : [];

    // Check if user is matched
    const matchedUser = includesUsers.find(
      (item) => item.id == appStore.state.userID
    );

    // Check if user role matches
    let matchedRole = false;
    for (let role of userRoles) {
      const match = includesRoles.find((item) => item.value == role);
      if (match) {
        matchedRole = true;
        break;
      }
    }

    const excludedRoles = remoteMenu.meta?.menu_settings?.excludes
      ? remoteMenu.meta?.menu_settings?.excludes.filter(
          (item) => item.type == 'role'
        )
      : [];
    const excludedUsers = remoteMenu.meta?.menu_settings?.excludes
      ? remoteMenu.meta?.menu_settings?.excludes.filter(
          (item) => item.type == 'user'
        )
      : [];

    // Check if user is excluded
    const matchedExcludedUser = excludedUsers.find(
      (item) => item.id == appStore.state.userID
    );

    // Check if user role matches
    let matchedExcludedRole = false;
    for (let role of userRoles) {
      const match = excludedRoles.find((item) => item.value == role);
      if (match) {
        matchedExcludedRole = true;
        break;
      }
    }

    // User is either matched by role or user id and not excluded by role or user id
    if (
      (matchedUser || matchedRole) &&
      !matchedExcludedUser &&
      !matchedExcludedRole
    ) {
      // Update menu
      menu.value = [...remoteMenu.meta?.menu_items];
      cacheMenu(remoteMenu.meta?.menu_items);
      filterMenu();
      return;
    }
  }
  // Menus applied so set default
  menu.value = [...OGmenu.value];
  cacheMenu('no_menus');
};

/**
 * Saves the menu into local storage
 */
const cacheMenu = (menu) => {
  const key = `uixpress_menu_${appStore.state.userID}`;
  const item = {
    value: menu,
    timestamp: new Date().getTime(),
  };
  localStorage.setItem(key, JSON.stringify(item));
};

// Function to fetch array from localStorage, return null if expired
function getMenuFromLocalStorage() {
  const key = `uixpress_menu_${appStore.state.userID}`;
  const item = localStorage.getItem(key);

  if (!item) {
    return null;
  }

  const parsedItem = JSON.parse(item);
  const now = new Date().getTime();
  const oneHour = 60 * 60 * 1000; // 1 hour in milliseconds

  if (now - parsedItem.timestamp > oneHour) {
    localStorage.removeItem(key);
    return null;
  }

  return parsedItem.value;
}

/**
 * Removes items that no longer exist and adds new items
 *
 * @since 1.0.9
 */
const filterMenu = () => {
  let activeItem =
    document.querySelector('#adminmenu a.current') ||
    document.querySelector('#adminmenu .wp-menu-open a') ||
    document.querySelector("#adminmenu a[aria-current='page']");
  let activeURL = '';

  // Get active items URL
  if (activeItem) {
    if (activeItem.tagName && activeItem.tagName.toLowerCase() === 'a') {
      activeURL = activeItem.getAttribute('href');
    }
  }

  // Remove items that no longer exist
  menu.value = menu.value
    .filter((item) => checkIfItemExists(item.id) || item.custom)
    .map((item) => ({ ...item, active: activeURL == item.url }));

  // Remove sub items that no longer exist
  for (let toplevel of menu.value) {
    // Bail if it's a sep
    if (toplevel.type == 'separator' || !Array.isArray(toplevel.submenu))
      continue;
    toplevel.submenu = toplevel.submenu
      .filter((item) => checkIfItemExists(item.id) || item.custom)
      .map((item) => ({ ...item, active: activeURL == item.url }));
  }

  // Check for items that are new and push to the menu
  for (let [index, toplevel] of OGmenu.value.entries()) {
    // Push to the menu if it's new
    if (!isItemInCustomMenu(toplevel.id)) {
      menu.value.splice(index, 0, toplevel);
    }

    // Continue if there is no submenu
    if (!toplevel.submenu) continue;

    // Loop submenu items to check for new items
    for (let [subindex, sub] of toplevel.submenu.entries()) {
      // Item exists in the menu so continue
      if (isItemInCustomMenu(sub.id)) continue;

      // Find correct top level item to push it to
      const parent = menu.value.find((item) => item.id == toplevel.id);

      if (parent) {
        parent.submenu.splice(subindex, 0, sub);
      } else {
        toplevel.submenu = [sub];
        menu.value.splice(index, 0, toplevel);
      }
    }
  }
};

const isItemInCustomMenu = (id) => {
  const existingTopLevel = menu.value.find((item) => item.id == id);
  const existingSubLevel = flattenedCustomMenuSubItems.value.find(
    (item) => item.id == id
  );
  return existingTopLevel || existingSubLevel;
};

const checkIfItemExists = (id) => {
  const existingTopLevel = OGmenu.value.find((item) => item.id == id);
  const existingSubLevel = flattenedSubItems.value.find(
    (item) => item.id == id
  );
  return existingTopLevel || existingSubLevel;
};

/**
 * Checks if a given link is active.
 * First checks if window.location ends with the link URL, then falls back to existing logic.
 * @param {Object} link - The link object to check.
 * @param {string} link.url - The URL of the link
 * @param {boolean} link.active - Whether the link is marked as active
 * @param {Array} link.submenu - Optional submenu items
 * @returns {boolean} True if the link is active, false otherwise.
 */
const isActive = (link) => {
  // First check if current location matches the link URL
  if (link?.url && isCurrentLocationMatch(link.url)) {
    return true;
  }

  // Fall back to existing active property
  if (link.active) return true;

  // Check submenu items
  if (link.submenu) {
    return link.submenu.find((item) => {
      // Check location match first for submenu items too
      if (item?.url && isCurrentLocationMatch(item.url)) {
        return true;
      }
      return item.active;
    });
  }
};

/**
 * Updates the width of the panel.
 * @param {number} newWidth - The new width to set for the panel.
 */
const updatePanelWidth = (newWidth) => {
  panelWidth.value = newWidth;
};

/**
 * Sets the menu
 */
const setMenu = async () => {
  const menuNode = document.querySelector('#adminmenumain');
  const { processedMenu } = await processMenu(menuNode);

  OGmenu.value = processedMenu;
};

const flattenedCustomMenuSubItems = computed(() => {
  return menu.value.reduce((acc, item) => {
    if (Array.isArray(item.submenu)) {
      acc.push(...item.submenu);
    }
    return acc;
  }, []);
});

const flattenedSubItems = computed(() => {
  return OGmenu.value.reduce((acc, item) => {
    if (Array.isArray(item.submenu)) {
      acc.push(...item.submenu);
    }
    return acc;
  }, []);
});

const shouldShowSubMenu = (link) => {
  // No submenu
  if (!Array.isArray(link.submenu)) return false;
  // Empty submenu
  if (!link.submenu.length) return false;
  // Link is active
  if (isActive(link) || link.active) return true;

  return false;
};

watchEffect(() => {
  let extra = '1px';
  if (returnLayout.value == 'bubble') {
    extra = '1rem';
  }

  if (isMenuMinified.value && menupanel.value) {
    setTimeout(() => {
      const rect = menupanel.value.getBoundingClientRect();
      const widther = rect.width;
      document.documentElement.style.setProperty(
        '--uixp-menu-width',
        `calc(${widther}px + ${extra})`
      );
    }, 0);
  } else {
    let widther = 240;
    widther = panelWidth.value;
    document.documentElement.style.setProperty(
      '--uixp-menu-width',
      `calc(${widther}px + ${extra})`
    );
  }
});

watch(
  () => menu.value,
  () => {
    appStore.state.adminMenu = menu.value;
  },
  { deep: true }
);

/**
 * Watches for remote site selection changes and updates menu accordingly
 */
watch(
  () => selectedRemoteSite.value,
  () => {
    // Menu will automatically update via returnMenuItems computed property
    // This watcher ensures reactivity when switching sites
  }
);

/**
 * Loads sidebar render components via filter hooks
 */
onMounted(async () => {
  // Dispatch event for plugins to register sidebar components
  const event = new CustomEvent('uixpress/sidebar/ready');
  document.dispatchEvent(event);

  await nextTick();

  // Register pre-menu components
  preMenuComponents.value = await applyFilters(
    'uixpress/sidebar/render/premenu',
    preMenuComponents.value
  );

  // Register post-menu components
  postMenuComponents.value = await applyFilters(
    'uixpress/sidebar/render/postmenu',
    postMenuComponents.value
  );
});

const updateMenuFromFetch = async () => {
  try {
    await nextTick();

    const url = `${appStore.state.adminUrl}/admin.php?page=plugin-manager`;
    const response = await fetch(url);

    // Generic error
    if (!response.ok) {
      return;
    }

    // Get HTML content
    const htmlContent = await response.text();

    // Create a DOM parser to handle the HTML content
    const parser = new DOMParser();
    const doc = parser.parseFromString(htmlContent, 'text/html');

    // Find the metrics script tag
    const menuNode = doc.getElementById('adminmenumain');

    if (!menuNode) return;

    // Stops plugin redirection links from altering the active link
    const links = menuNode.querySelectorAll('a');
    for (let link of links) {
      link.classList.remove('current');
      link.classList.remove('wp-menu-open');
      if (link.getAttribute('aria-current') == 'page') {
        link.removeAttribute('aria-current');
      }

      if (link.classList.contains('toplevel_page_plugin-manager')) {
        link.classList.add('current');
      }
    }

    const linksListItems = menuNode.querySelectorAll('li');
    for (let listItem of linksListItems) {
      listItem.classList.remove('current');
      listItem.classList.remove('wp-menu-open');
    }

    // Update menu
    const existingMenuNode = document.getElementById('adminmenumain');
    existingMenuNode.replaceWith(menuNode);
    await nextTick();
    // Process new menu
    await setMenu();
    filterMenu();
  } catch (err) {}
};

document.addEventListener('uipx-plugin-activated', updateMenuFromFetch);
document.addEventListener('uipx-plugin-deactivated', updateMenuFromFetch);

getMenuForUser();
</script>

<template>
  <div
    class="flex flex-col gap-1 w-full h-full relative max-h-full overflow-hidden bg-zinc-50 dark:bg-zinc-950"
    ref="menupanel"
    :class="isMenuMinified ? 'py-4' : 'p-4 px-6 pt-2'"
    :style="isMenuMinified ? 'width:auto' : `width:${panelWidth}px`"
    v-if="OGmenu"
  >
    <div
      class="flex flex-row items-center"
      :class="
        isMenuMinified ? 'place-content-center mb-3' : 'place-content-between'
      "
    >
      <template v-if="!isMenuMinified">
        <a
          :href="appStore.state.adminUrl"
          class="pl-3 place-self-start"
          v-if="!appStore.state?.uixpress_settings?.logo"
        >
          <AppIcon icon="uixpress" class="text-5xl dark:text-white" />
        </a>

