<script setup>
import { ref, watchEffect, nextTick, onMounted } from 'vue';
// Comps
import AppIcon from '@/components/utility/icons/index.vue';
import AppButton from '@/components/utility/app-button/index.vue';
import Drawer from '@/components/utility/drawer/index.vue';

const notificationsCount = ref(0);
const notificationList = ref(null);
const isOpen = ref(false);
const open = ref(false);
const parentOpen = ref(false);
const panel = ref(null);
const notificationNodes = ref(null);
const mounted = ref(false);
const isJiggling = ref(false);
const notificationsList = ref(null);
const noticesMoved = ref(false);
const panelWidth = ref(800);
const targetSelector =
  ".notice:not(table .notice):not(#message.notice):not(.hide-if-js .notice):not(.themes .notice):not(.notice.hidden):not(.notice[aria-hidden='true']):not(.notice.hide-if-js)";

const getNotificationsCount = async () => {
  const notis = document.querySelectorAll(targetSelector);

  // No notifications
  if (!notis) {
    notificationsCount.value = 0;
    return;
  }

  notificationsCount.value = 0;
  for (let item of [...notis]) {
    if (
      !item.innerText ||
      item.id == 'setting-error-settings_updated' ||
      !isVisible(item)
    )
      continue;
    notificationsCount.value++;
  }

  if (notificationsCount.value > 0) {
    await nextTick();
    hideNotifications();

    isJiggling.value = true;
    setTimeout(() => {
      isJiggling.value = false;
    }, 500);
  }
};

const hideNotifications = () => {
  const notices = document.querySelectorAll(targetSelector);
  for (let item of [...notices]) {
    item.style.display = 'none';
  }
};

const isVisible = (element) => {
  const style = window.getComputedStyle(element);
  return style.display !== 'none' && style.visibility !== 'hidden';
};

const setNotifications = () => {
  if (noticesMoved.value) return;
  noticesMoved.value = true;

  if (!notificationList.value || notificationNodes.value) return;

  notificationNodes.value = document.querySelectorAll(targetSelector);

  if (!notificationNodes.value) return;

  for (let item of [...notificationNodes.value]) {
    if (!item.innerText || item.id == 'setting-error-settings_updated')
      continue;
    notificationList.value.appendChild(item);
    item.style.display = 'block';
  }
};

watchEffect(() => {
  if (open.value) nextTick(() => (parentOpen.value = true));
  if (!open.value) nextTick(() => (parentOpen.value = false));
});

const stop = watchEffect(async () => {
  if (!notificationList.value) return;

  await nextTick();
  setTimeout(() => {
    setNotifications();
    console.log('setNotifications');
    stop();
  }, 100);
});

onMounted(() => {
  mounted.value = true;
  getNotificationsCount();
});
</script>

<template>
  <AppButton
    type="transparent"
    :title="__('Notifications', 'uixpress')"
    class="max-md:hidden"
    @click="isOpen = true"
  >
    <div class="relative" :class="{ 'animate-jiggle': isJiggling }">
      <AppIcon icon="notifications-2" class="text-2xl" />

      <Transition>
        <span
          v-if="notificationsCount > 0"
          class="absolute top-0 right-0 inline-flex items-center justify-center text-xs font-bold leading-none text-rose-100 transform right-0 top-0 bg-rose-600 dark:bg-rose-700 rounded-full aspect-square h-2"
        >
        </span>
      </Transition>
    </div>
  </AppButton>

  <KeepAlive>
    <Drawer
      v-model="isOpen"
      title="Notifications"
      :show-close-button="false"
      :show-header="false"
    >
      <div
        class="flex flex-col gap-6 p-8 max-w-full"
        id="uixp-notification-list"
        ref="notificationsList"
        :style="`width:${panelWidth}px`"
      >
        <div class="flex flex-row items-center place-content-between">
          <div class="text-2xl font-semibold text-zinc-900 dark:text-zinc-100">
            {{ __('Notifications', 'uixpress') }}
          </div>

          <div
            class="hover:bg-zinc-100 dark:hover:bg-zinc-800 p-1 rounded-lg cursor-pointer text-zinc-500 dark:text-zinc-400 hover:text-zinc-900 dark:hover:text-zinc-100 transition-all"
            :title="__('Close', 'uixpress')"
            @click="isOpen = false"
          >
            <AppIcon icon="close" class="text-xl" />
          </div>
        </div>
        <div
          v-if="!notificationsCount"
          class="leading-loose p-2 rounded-lg bg-emerald-100 dark:bg-emerald-800 border border-solid border-emerald-200 dark:border-emerald-700 dark:text-zinc-300"
        >
          {{ __('No notifications', 'uixpress') }}
        </div>
        <div class="flex flex-col gap-3" ref="notificationList"></div>
      </div>
    </Drawer>
  </KeepAlive>
</template>

<style>
#uixp-body
  .notice:not(table .notice):not(#message.notice):not(.hide-if-js .notice):not(
    .themes .notice
  ):not(#setting-error-settings_updated) {
  display: none !important;
}

#uixp-notification-list .notice {
  margin: 0 !important;
}

@keyframes jiggle {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(-8deg);
  }

  50% {
    transform: rotate(7deg);
  }

  75% {
    transform: rotate(-8deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

.animate-jiggle {
  animation: jiggle 0.5s ease-in-out;
}
</style>
