<script setup>
import { defineProps, computed } from 'vue';
import { returnOriginalLinkAttribute } from './returnOriginalLinkAttribute.js';

const props = defineProps(['link', 'active']);

// Computed property for notification count to handle different data types
const notificationCount = computed(() => {
  const count = returnOriginalLinkAttribute(
    props.link,
    'notifications',
    props.link.notifications
  );
  if (!count) return null;

  // Handle numeric values and ensure they're reasonable
  const numCount = parseInt(count);
  if (isNaN(numCount) || numCount <= 0) return null;

  // Cap at 99+ for better UI
  return numCount > 99 ? '99+' : numCount.toString();
});

// Computed property for the menu name with fallback
const menuName = computed(() => {
  return (
    props.link.settings?.name ||
    returnOriginalLinkAttribute(props.link, 'name', props.link.name) ||
    ''
  );
});
</script>

<template>
  <div class="pl-12 flex-grow flex items-center justify-between gap-3 min-w-0">
    <!-- Menu name with improved typography -->
    <div
      class="flex-1 min-w-0 font-medium group-hover:text-zinc-900 dark:group-hover:text-zinc-50 transition-colors duration-200 leading-relaxed"
      :class="
        active
          ? 'text-zinc-950 dark:text-white'
          : 'text-zinc-700 dark:text-zinc-500'
      "
      v-html="menuName"
    />

    <!-- Enhanced notification badge -->
    <div
      v-if="notificationCount"
      class="flex-shrink-0 inline-flex items-center justify-center min-w-[1.25rem] h-5 px-1.5 text-xs font-semibold leading-none bg-gradient-to-r from-indigo-500 to-indigo-600 dark:from-indigo-400 dark:to-indigo-500 text-white rounded-full shadow-sm ring-2 ring-white dark:ring-zinc-800 transition-all duration-200 group-hover:shadow-md group-hover:scale-105"
      :class="notificationCount.length > 2 ? 'px-2' : ''"
    >
      {{ notificationCount }}
    </div>
  </div>
</template>

<style scoped>
/* Ensure proper text rendering for dynamic content */
div[v-html] {
  word-break: break-word;
  overflow-wrap: break-word;
}

/* Smooth badge animations */
.notification-badge {
  transform-origin: center;
}

/* Handle long text gracefully */
@supports (display: -webkit-box) {
  div[v-html] {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
}

/* Micro-interaction for notification badge */
.group:hover .notification-badge {
  animation: subtle-pulse 0.6s ease-in-out;
}

@keyframes subtle-pulse {
  0%,
  100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }
}
</style>
