<script setup>
import { defineProps, computed, inject } from 'vue';
import { returnOriginalLinkAttribute } from './returnOriginalLinkAttribute.js';
import AppIcon from '@/components/utility/icons/index.vue';

const props = defineProps([
  'sublink',
  'isFocused',
  'isActive',
  'isFavorite',
  'hideFavorite',
]);

// Inject addFavorite from parent component
const addFavorite = inject('addFavorite', null);

// Helps trigger events on original nodes
const triggerBaseNode = (evt) => {
  if (!props.sublink.og_node) return;

  const event = new MouseEvent('mousedown', {
    bubbles: true,
    cancelable: true,
    view: window,
  });
  props.sublink.og_node.dispatchEvent(event);
};

// Computed property for notification count to handle different data types
const notificationCount = computed(() => {
  const count = returnOriginalLinkAttribute(
    props.sublink,
    'notifications',
    props.sublink.notifications
  );
  if (!count) return null;

  // Handle numeric values and ensure they're reasonable
  const numCount = parseInt(count);
  if (isNaN(numCount) || numCount <= 0) return null;

  // Cap at 99+ for better UI
  return numCount > 99 ? '99+' : numCount.toString();
});

/**
 * Computed property to ensure the URL is properly formatted
 * Decodes the URL if it's already encoded to prevent double-encoding
 * @returns {string} The properly formatted URL
 */
const formattedUrl = computed(() => {
  if (!props.sublink?.url) return '';
  
  const url = props.sublink.url;
  
  // Check if URL contains encoded characters that shouldn't be encoded
  // (e.g., %2F for / in query parameters)
  // Only decode if we detect encoded characters
  if (url.includes('%')) {
    try {
      // Decode the URL to ensure it's in raw format
      // This prevents double-encoding issues where URLs like
      // admin.php?page=wc-admin&path=/analytics/overview
      // get rendered as admin.php?page=wc-admin&path=%2Fanalytics%2Foverview
      const decoded = decodeURIComponent(url);
      // Only use decoded version if it's different and valid
      return decoded;
    } catch (e) {
      // If decoding fails (invalid encoding), return original
      return url;
    }
  }
  
  // No encoded characters detected, return as-is
  return url;
});
</script>

<template>
  <a
    :href="formattedUrl"
    :id="sublink.sub_id"
    class="unset-all flex flex-row p-1 px-4 rounded-lg items-center cursor-pointer gap-1 group transition-all"
    :class="
      isActive || isFocused
        ? 'text-zinc-900 dark:text-white active'
        : ' hover:bg-zinc-100 hover:dark:bg-zinc-800 dark:text-zinc-500'
    "
    @click="triggerBaseNode($event)"
  >
    <div
      class="group-hover:text-zinc-950 dark:group-hover:text-zinc-100 transition-color flex-grow text-base grow truncate flex flex-row items-center gap-4"
    >
      <div
        v-html="
          sublink.settings?.name ||
          returnOriginalLinkAttribute(sublink, 'name', sublink.name)
        "
        class="truncate"
      ></div>
      <!-- Enhanced notification badge -->
      <div
        v-if="notificationCount"
        class="flex-shrink-0 inline-flex items-center justify-center min-w-[1.125rem] h-4 px-1 text-xs font-semibold leading-none bg-gradient-to-r from-indigo-500 to-indigo-600 dark:from-indigo-400 dark:to-indigo-500 text-white rounded-full shadow-sm ring-1 ring-white dark:ring-zinc-800 transition-all duration-200 group-hover:scale-105"
        :class="notificationCount.length > 2 ? 'px-1.5' : ''"
      >
        {{ notificationCount }}
      </div>
    </div>

    <!-- Favorite icon icon -->
    <AppIcon
      v-if="!isFavorite && !hideFavorite && addFavorite"
      icon="star"
      class="opacity-0 group-hover:opacity-100 text-base transition-opacity"
      @click.prevent.stop="addFavorite(sublink)"
    />

    <!-- Open in new -->
    <AppIcon
      v-if="sublink.settings?.open_new"
      icon="open_new"
      :class="isActive || isFocused ? 'opacity-100' : ''"
      class="opacity-0 group-hover:opacity-100 text-base transition-opacity"
    />
  </a>
</template>

<style scoped>
/* Ensure proper text rendering for dynamic content */
div[v-html] {
  word-break: break-word;
  overflow-wrap: break-word;
}

/* Handle long text gracefully */
@supports (display: -webkit-box) {
  div[v-html] {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
}

/* Micro-interaction for notification badge */
.group:hover .notification-badge {
  animation: subtle-pulse 0.6s ease-in-out;
}

@keyframes subtle-pulse {
  0%,
  100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }
}
</style>
